.track-field {
  min-width: 500px;
  min-height: 500px;
  border: 1px solid black;
  background-color: white;
}

.node-container {
  width: 100px;
  height: 100px;
  background-color: lightgray;

  -webkit-box-shadow: 0px 0px 15px 1px rgba(0, 0, 0, 0.1);
  -moz-box-shadow: 0px 0px 15px 1px rgba(0, 0, 0, 0.1);
  box-shadow: 0px 0px 15px 1px rgba(0, 0, 0, 0.1);
}
.node-container:hover {
  cursor: pointer;
}
.node-container:active {
  -webkit-box-shadow: 0px 0px 15px 1px rgba(0, 0, 0, 0.3);
  -moz-box-shadow: 0px 0px 15px 1px rgba(0, 0, 0, 0.3);
  box-shadow: 0px 0px 15px 1px rgba(0, 0, 0, 0.3);
}

.track-field > h2 {
  text-align: center;
}

.node-text {
  min-height: 100%;
  text-align: center;
  line-height: 100px;
  user-select: none;
}

.btn-bar {
  display: flex;
  justify-content: center;
  min-width: 100%;
}

.box-control {
  padding: 0.5em;
  background-color: rgb(111, 176, 206);
  width: 100%;
  margin: 0.5em;
  text-align: center;
  border-radius: 1em;
  font-size: 15pt;
  font-weight: 600;
}
.box-control:hover {
  cursor: pointer;
  background-color: rgb(147, 193, 214);
}
